@import url("https://fonts.googleapis.com/css?family=Poppins");
body {
  margin: 0;
  background-color: #06032c;
}
a {
  text-decoration: none;
}
* {
  font-family: 'Poppins', sans-serif;
  color: white;
}
ul {
  list-style: none;
}
main {
  text-align: center;
}
header span {
  display: block;
  position: relative;
  font-size: 20px;
  line-height: 1;
  letter-spacing: .02em;
  font-weight: 400;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-top: 16px;
}



